








































































































import { Component, Vue } from 'vue-property-decorator';

@Component({})
export default class Swiper extends Vue {
  activeIndex: number = 0;

  change(index) {
    this.activeIndex = index;
  }
  back() {
    if(this.activeIndex <= 0) return;
    this.activeIndex -= 1;
  }
  next() {
    if(this.activeIndex >= 2) return;
    this.activeIndex += 1;
  }
}
