




import { Component, Vue } from "vue-property-decorator";

@Component({})
export default class BackUp extends Vue {
  isShow: boolean = false;

  toTop(i) {
    var c: any = null;
    document.documentElement.scrollTop -= i;

    if (document.documentElement.scrollTop > 0) {
      c = setTimeout(() => this.toTop(i), 16);
    } else {
      clearTimeout(c);
    }
  }

  judgeShow() {
    var vm = this;
    if (document.documentElement.scrollTop > document.documentElement.clientHeight / 2) {
      vm.isShow = true;
    } else {
      vm.isShow = false;
    }
  }

  mounted() {
    window.addEventListener("scroll", this.judgeShow);
  }
  destroyed() {
    window.removeEventListener("scroll", this.judgeShow);
  }
}
