


















import { Vue, Component, Prop } from "vue-property-decorator";

@Component
export default class swiper extends Vue {
  @Prop({ required: true, type: Number }) total: Number;
  @Prop({ required: true, type: Number }) limit: Number;
  @Prop({ required: true, type: Number }) width: Number;
  @Prop({ required: false, type: Number, default: -29 }) _left: Number;
  @Prop({ required: false, type: Number, default: -29 }) _right: Number;

  left: number = 0;

  get totalPage(): number {
    const { total, limit } = this;
    return Math.ceil(total / limit);
  }

  get style() {
    const { totalPage, left, width } = this;
    return `width: ${totalPage}00%; transform: translateX(${left * width}px)`;
  }

  get minLeft() {
    const { totalPage } = this;
    return totalPage - 1;
  }

  back() {
    const { left } = this;
    if (left >= 0) return;
    this.left = left + 1;
  }

  next() {
    const { left, minLeft } = this;
    if (left <= -minLeft) return;
    this.left = left - 1;
  }
}
